
.masonry {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 0.5rem;
  margin: 0 auto;
}

.brick-group {
  flex: 1;
  min-width: 250px;
  max-width: 300px;
  grid-gap: 0.5rem;
  display: flex;
  flex-direction: column;
}

.brick {
  width: 100%;
  border: 1px solid rgb(var(--color-foreground), 0.18);
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  height: calc((var(--brick-height) * 60px) + ((var(--brick-height) - 1) * 10px));

  &__background {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%) rotate(var(--bkg-rotation));
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__foreground {
    padding: 1rem;
    z-index: 1;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(4px);
    color: rgb(var(--color-light));
    background-color: rgba(var(--color-background-secondary), 0.3);
  }
}

@media all and (min-width: 1000px) {
  .brick {
    height: calc((var(--brick-height) * 80px) + ((var(--brick-height) - 1) * 10px));
  }
}