.resume__style1 {
  .resume {

    &__header {
      display: flex;
      padding: 1rem 0;
      position: sticky;
      top: 0;
      background-color: rgb(var(--color-background));

      &__part {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }

    &__contents {
      padding: 0.5rem 2rem 4rem;

      hr {
        margin: 1.5rem 0;
      }

      h2 {
        margin: 1rem 0;
      }
    }

    &__history-item {
      margin: 1rem 0 2rem;

      &__header {
        display: flex;
        align-items: center;
        margin: 0 0 0.5rem;

        >div {
          flex: 1;
        }
      }

      &__body {
        margin: 0 2rem;

        ul {
          list-style-type: disc;
          font-size: 0.8em;
        }
      }
    }
  }
}

@keyframes rainbow-animation {
  0% {
    background-color: var(--rainbow-1);
  }

  20% {
    background-color: var(--rainbow-2);
  }

  40% {
    background-color: var(--rainbow-3);
  }

  60% {
    background-color: var(--rainbow-4);
  }

  80% {
    background-color: var(--rainbow-5);
  }

  100% {
    background-color: var(--rainbow-1);
  }
}