header {
  background-color: rgb(var(--color-background));
  box-shadow: 0 0 4px 4px rgba(var(--color-foreground), 0.26);
  height: 70px;
  --rainbow-height: 5px;
  position: sticky;
  top: 0;
  z-index: 5;

  .rainbow-border {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: var(--rainbow-height);
    background: linear-gradient(90deg, var(--rainbow-1), var(--rainbow-2), var(--rainbow-3), var(--rainbow-4), var(--rainbow-5));
  }
}

.header__content {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1080px;
  margin: 0 auto;

  h2 {
    display: none;
  }
}


@media all and (min-width: 700px) {
  .header__content h2 {
    display: block;
  }
}