
footer {
  position: relative;
  --rainbow-height: 5px;

  .rainbow-border {
    position: absolute;
    top: 0;
    width: 100%;
    height: var(--rainbow-height);
    background: linear-gradient(90deg, var(--rainbow-1), var(--rainbow-2), var(--rainbow-3), var(--rainbow-4), var(--rainbow-5));
  }
}

.footer__content {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: var(--rainbow-height);
  height: calc(50px + var(--rainbow-height));
  grid-gap: 1rem;
  opacity: 0.5;

  img {
    height: 50%;
    aspect-ratio: 1;
  }
}
