.glass-popover {
  border: 1px solid rgb(var(--color-foreground), 0.18);
  box-shadow: 0 4px 16px 0 rgba(var(--color-foreground), 0.37);
  border-radius: 5px;
  width: 95%;
  padding: 0.5rem 0.5rem 1.5rem;
  color: rgb(var(--color-light));
  background-color: rgba(var(--color-foreground), 0.25);
  max-width: 500px;
  animation: 0.1s linear forwards open-popover;

  &__backdrop {
    background-color: rgba(var(--color-background), 0.25);
    backdrop-filter: blur(8px);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 5;
  }

  &__contents {
    padding: 0 0.5rem;
  }

  li {
    margin: 0.5rem 0 0;
  }

  &__underline {
    height: 2px;
    width: 100%;
    background-color: rgb(var(--color-mid));
  }
  &__underline-fill {
    height: 100%;
    width: calc(var(--underline-width) * 1%);
    background-color: rgb(var(--color-light));
  }

  &__close {
    position: fixed;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@keyframes open-popover {
  0% {
    transform: scale(0.8);
  }
  30% {
    transform: scale(1.1);
  }
  60% {
    transform: scale(1);
  }
}

@media all and (min-width: 700px) {
  .glass-card__foreground {
    font-size: 1.25rem;
  }
}

@media all and (min-width: 1000px) {
  .glass-card__foreground {
    font-size: 1.5rem;
  }
}