@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,600;0,900;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Serif+4:ital,wght@0,400;0,600;0,700;1,400&display=swap');

:root {
  --color-foreground: 17, 24, 39;
  --color-foreground-secondary: 42, 56, 85;
  --color-background-secondary: 175, 173, 168;
  --color-background: 243, 244, 246;
  --color-light: 243, 244, 246;
  --color-mid: 46, 60, 90;
  --color-dark: 17, 24, 39;
  --user-font-scale: 1rem;
  --rainbow-1: #632bf3;
  --rainbow-2: #f122c8;
  --rainbow-3: #f16022;
  --rainbow-4: #9ef344;
  --rainbow-5: #44d3f3;

  --red: #c93030;
}

.xx-large {
  font-size: 5rem;
}

.x-large {
  font-size: 3.5rem;
}

.large {
  font-size: 2rem;
}

.big {
  font-size: 1.5rem;
}

.serif {
  font-family: 'Source Serif 4', serif;
}

.italics {
  font-style: italic;
}

.text-gray {
  color: rgb(var(--color-foreground-secondary))
}

h2,
h3 {
  font-weight: 600;
}

ul {
  list-style-type: none;
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  width: 100%;
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  background: rgb(var(--color-background));
  color: rgb(var(--color-foreground));
  font-size: clamp(0.875rem, 0.4626rem + 1.0309vw + var(--user-font-scale), 1.125rem);
  user-select: none;

  >div {
    min-height: inherit;
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-foreground: 250, 246, 238;
    --color-foreground-secondary: 175, 173, 168;
    --color-background-secondary: 42, 56, 85;
    --color-background: 17, 24, 39;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

@media all and (max-width: 700px) {
  .hide-phone {
    display: none;
  }
}

@media all and (min-width: 700px) {
  .hide-phone {
    display: block;
  }

  .hide-tablet {
    display: none;
  }
}

@media all and (min-width: 1000px) {
  .hide-tablet {
    display: block;
  }

  .hide-pc {
    display: none;
  }
}