
.glitched-title {
  position: relative;

  .layer {
    mix-blend-mode: exclusion;

    &.red {
      position: absolute;
      top: 0;
      left: 0;
      color: #FF0000;
      transform: translate(calc(var(--displacement-x) * -0.25em), calc(var(--displacement-y) * 0.25em));
      animation: 0.1s glitch-out linear infinite;
    }

    &.green {
      color: #00FF00;
    }

    &.blue {
      position: absolute;
      top: 0;
      left: 0;
      color: #0000FF;
      transform: translate(calc(var(--displacement-x) * 0.25em), calc(var(--displacement-y) * -0.25em));
      animation: 0.1s glitch-out linear infinite;
    }
  }
}

@keyframes glitch-out {
  0% {
    left: 0;
  }
  25% {
    left: -1px;
  }
  75% {
    left: 1px;
  }
  100% {
    left: 0;
  }
}