
.split-title {
  display: flex;
  width: fit-content;
  margin: 0 auto;
  position: relative;
  line-height: 0.85;

  &__cutout {
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(90deg,
            var(--rainbow-1), var(--rainbow-2),
            var(--rainbow-3), var(--rainbow-4),
            var(--rainbow-5), var(--rainbow-1)
    );
    color: rgb(var(--color-background));
    animation: cutout-movement 20s infinite ease-in-out;
  }
}

@keyframes cutout-movement {
  0% {
    clip-path: polygon(var(--pos-left-1) 0%, var(--pos-left-1) 100%, var(--pos-right-1) 100%, var(--pos-right-1) 0%);
  }
  35% {
    clip-path: polygon(var(--pos-left-2) 0%, var(--pos-left-2) 100%, var(--pos-right-2) 100%, var(--pos-right-2) 0%);
  }
  70% {
    clip-path: polygon(var(--pos-left-3) 0%, var(--pos-left-3) 100%, var(--pos-right-3) 100%, var(--pos-right-3) 0%);
  }
  100% {
    clip-path: polygon(var(--pos-left-1) 0%, var(--pos-left-1) 100%, var(--pos-right-1) 100%, var(--pos-right-1) 0%);
  }
}