
.section-contact {
  padding: 0 0.5rem;
  margin: 5rem auto;
  max-width: 700px;

  h2 {
    width: fit-content;
    margin: 0 auto 10px;
  }

  &__contents {
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 0.5rem;

    .redirect-btn {
      min-width: 250px;
      max-width: 300px;
      flex: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      box-shadow: 0 0 2px 2px rgba(var(--color-foreground), 0.15);
      border-radius: 5px;
      padding: 0.5rem;
      transition: 0.2s;
      cursor: pointer;

      &:hover {
        background-color: rgba(var(--color-foreground), 0.1);
        box-shadow: 0 0 2px 2px rgba(var(--color-foreground), 0.2);
      }
    }

    .logo-div {
      --logo-height: 30px;
      height: var(--logo-height);
      aspect-ratio: 1;
      background: rgb(var(--color-foreground));

      &.github-logo {
        mask-image: url(../../../public/assets/github.svg);
        mask-repeat: no-repeat;
        mask-position: center;
      }

      &.linkedin-logo {
        mask-image: url(../../../public/assets/linkedin.svg);
        mask-repeat: no-repeat;
        mask-position: center;
      }

      &.mail-logo {
        mask-image: url(../../../public/assets/envelope.svg);
        mask-repeat: no-repeat;
        mask-position: center;
      }

      &.download-logo {
        height: calc(var(--logo-height) * 0.9);
        mask-image: url(../../../public/assets/download.svg);
        mask-repeat: no-repeat;
        mask-position: center;
      }
    }
  }
}

@media all and (min-width: 1000px) {
  .section-contact__contents {
    max-width: 1000px;
  }
}