.glass-card {
  border: 1px solid rgb(var(--color-foreground), 0.18);
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  margin: 0 auto 2rem;
  transition: width 0.2s;
  cursor: pointer;

  &:hover {
    .glass-card__background {
      transform: translateX(var(--displacement));
    }

    .glass-card__foreground {
      background-color: rgba(var(--color-foreground), 0.15);
    }
  }

  &__background {
    width: 100%;
    height: 100%;
    transition: transform 0.2s;
    position: absolute;

    .circle {
      position: absolute;
      top: calc(var(--position-y) * 1%);
      left: calc(var(--position-x) * 1%);
      width: calc(var(--size) * 1px);
      aspect-ratio: 1;
      border-radius: calc(var(--size) * 0.5px);
      transform: translate(-50%, -50%);

      &.color-1 {
        background-color: var(--rainbow-1);
      }

      &.color-2 {
        background-color: var(--rainbow-2);
      }

      &.color-3 {
        background-color: var(--rainbow-3);
      }

      &.color-4 {
        background-color: var(--rainbow-4);
      }

      &.color-5 {
        background-color: var(--rainbow-5);
      }
    }
  }

  &__foreground {
    padding: 1rem;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(4px);
    color: rgb(var(--color-light));
    transition: background-color 0.2s;
    background-color: rgba(var(--color-foreground), 0.3);
    display: flex;
    align-items: center;
    justify-content: space-between;

    img {
      filter: invert(1);
    }
  }
}


@media all and (min-width: 700px) {
  .glass-card__foreground {
    font-size: 1.25rem;
  }
}

@media all and (min-width: 1000px) {
  .glass-card__foreground {
    font-size: 1.5rem;
  }
}