
.tabbed-menu {
  width: 100%;
}

.tabbed-menu {

  &__tabs {
    grid-gap: 1rem;
    margin: 0.5rem auto;
    display: flex;
    flex-direction: column;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    background-color: rgb(var(--color-background-secondary));
    box-shadow: inset 0 0 2px 2px #00000050;
    position: relative;
    width: 100%;
  }

  &__tab {
    height: calc(50px - 0.5rem);
    width: 100%;
    text-align: center;
    padding: 0.5rem 1.5rem;
    z-index: 1;
    cursor: pointer;
  }

  &__tab-selector {
    height: calc(50px - 0.5rem);
    border-radius: 5px;
    width: calc(100% - 2rem);
    position: absolute;
    top: calc(0.5rem + (var(--selected-item) * (42px + 1rem)));
    background-color: rgb(var(--color-background));
    transition: top 0.2s;
  }
}

@media all and (min-width: 700px) {
  .tabbed-menu {
    &__tabs {
      padding: 0.25rem 1rem;
      width: fit-content;
      height: 50px;
      flex-direction: row;
    }

    &__tab {
      width: 200px;
    }

    &__tab-selector {
      top: 0.25rem;
      left: calc(1rem + (var(--selected-item) * (200px + 1rem)));
      width: 200px;
      transition: left 0.2s;
    }
  }
}

.tabbed-menu {

  &__tab-body {
    margin-block: 0;
    padding-inline: 0;
    max-height: 400px;
    overflow-y: auto;
    margin: 0 auto;

    hr {
      width: 80%;
      margin: 0 auto;
    }
  }

  &__list-item {
    display: flex;
    flex-direction: column;
    margin: 0.5rem 0;
    padding-left: 1.2rem;
    position: relative;

    span {
      margin-left: 1rem;
    }
  }

  &__list-index {
    position: absolute;
    top: 0.15rem;
    left: 0;
    font-size: 1rem;
  }
}

.tabbed-menu__tab-body {
  &::-webkit-scrollbar {
    width: 0.5rem;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: rgb(var(--color-foreground));
  }
}