.resume__popup {
  position: fixed;
  overflow-y: auto;
  top: 10%;
  left: 10%;
  width: 80%;
  height: 80%;
  background-color: rgb(var(--color-background));
  z-index: 2;
  border-radius: 5px;
  box-shadow: 0 0 5px rgb(var(--color-foreground-secondary));
}

.resume__control-buttons {
  position: fixed;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  grid-gap: 10px;
}

.resume__download {

  .download-logo {
    --logo-height: 25px;
    height: var(--logo-height);
    aspect-ratio: 1;
    background: white;

    height: calc(var(--logo-height) * 0.9);
    mask-image: url(../../../public/assets/download.svg);
    mask-repeat: no-repeat;
    mask-position: center;
    transition: transform 0.2s;
  }

  display: flex;
  align-items: center;
  grid-gap: 10px;
  cursor: pointer;

  background-color: var(--rainbow-1);
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;
  font-size: 1.2em;
  padding: 0.75rem 2.5rem;
  transition: transform 0.2s;

  &:hover {
    animation: rainbow-animation 2s infinite;
    transform: scale(1.1);

    .download-logo {
      transform: scale(1.2);
    }
  }
}

.resume__close {

  .close-logo {
    --logo-height: 25px;
    height: var(--logo-height);
    aspect-ratio: 1;
    background: white;

    height: calc(var(--logo-height) * 0.9);
    mask-image: url(../../../public/assets/close.svg);
    mask-repeat: no-repeat;
    mask-position: center;
    transition: transform 0.2s;
  }

  display: flex;
  align-items: center;
  grid-gap: 10px;
  cursor: pointer;

  background-color: rgb(var(--color-foreground-secondary));
  color: rgb(var(--color-background-secondary));
  border: none;
  outline: none;
  border-radius: 5px;
  font-size: 1.2em;
  padding: 0.75rem 2.5rem;
  transition: transform 0.2s;

  &:hover {
    transform: scale(1.1);

    .close-logo {
      transform: scale(1.2);
    }
  }
}

@import "./style1.scss";
@import "./style2.scss";