
.vibrating-title {
  position: relative;
  width: fit-content;

  .layer {
    animation: vibrate 2.5s linear infinite;
    mix-blend-mode: exclusion;

    &.red {
      position: absolute;
      top: 0;
      left: 0;
      color: #FF0000;
      --rotation-amount: 7deg;
      --rotation-amount-inverse: calc(var(--rotation-amount) * -1);
      --scale-amount: .92;
    }
    &.green {
      color: #00FF00;
      --rotation-amount: 5deg;
      --rotation-amount-inverse: calc(var(--rotation-amount) * -1);
      --scale-amount: .9;
    }
    &.blue {
      position: absolute;
      top: 0;
      left: 0;
      color: #0000FF;
      --rotation-amount: 3deg;
      --rotation-amount-inverse: calc(var(--rotation-amount) * -1);
      --scale-amount: .88;
    }
  }
}

@keyframes vibrate {
  0% {
    //color: var(--color-foreground);
    transform: rotate(0deg);
  }
  1% {
    //color: var(--rainbow-1);
    transform: rotate(var(--rotation-amount)) scale(var(--scale-amount));
  }
  2% {
    transform: rotate(var(--rotation-amount-inverse)) scale(var(--scale-amount));
  }
  3% {
    transform: rotate(var(--rotation-amount)) scale(var(--scale-amount));
  }
  4% {
    //color: var(--rainbow-1);
    transform: rotate(var(--rotation-amount-inverse)) scale(var(--scale-amount));
  }
  5% {
    //color: var(--color-foreground);
    transform: rotate(var(--rotation-amount));
  }
  6% {
    //color: var(--rainbow-2);
    transform: rotate(var(--rotation-amount-inverse)) scale(var(--scale-amount));
  }
  7% {
    transform: rotate(var(--rotation-amount));
  }
  8% {
    transform: rotate(var(--rotation-amount-inverse));
  }
  9% {
    //color: var(--rainbow-2);
    transform: rotate(var(--rotation-amount)) scale(var(--scale-amount));
  }
  10% {
    //color: var(--color-foreground);
    transform: rotate(0deg);
  }

  20% {
    //color: var(--color-foreground);
    transform: rotate(0deg);
  }
  21% {
    //color: var(--rainbow-4);
    transform: rotate(var(--rotation-amount)) scale(var(--scale-amount));
  }
  22% {
    transform: rotate(var(--rotation-amount-inverse));
  }
  23% {
    transform: rotate(var(--rotation-amount)) scale(var(--scale-amount));
  }
  24% {
    //color: var(--rainbow-4);
    transform: rotate(var(--rotation-amount-inverse));
  }
  25% {
    //color: var(--color-foreground);
    transform: rotate(var(--rotation-amount));
  }
  26% {
    //color: var(--rainbow-5);
    transform: rotate(var(--rotation-amount-inverse)) scale(var(--scale-amount));
  }
  27% {
    transform: rotate(var(--rotation-amount));
  }
  28% {
    transform: rotate(var(--rotation-amount-inverse));
  }
  29% {
    //color: var(--rainbow-5);
    transform: rotate(var(--rotation-amount)) scale(var(--scale-amount));
  }
  30% {
    //color: var(--color-foreground);
    transform: rotate(0deg);
  }
}
