
.segmented-title {
  position: relative;

  .layer {
    background-color: rgb(var(--color-background));
    padding: 0 2rem;
    border-radius: 10px;
    width: var(--text-container-width);

    &.scrambled {
      border: 1px solid rgba(var(--color-foreground), 0.5);
      position: absolute;
      top: 0;
      transition: 0.3s;
      transform: rotate3d(1, 0, 0, 0deg);

      &.hidden {
        transform: rotate3d(1, 0, 0, -90deg);
      }
    }

    &.unscrambled {
      opacity: 0;
      transition: 1s 0.2s;

      &.hidden {
        opacity: 1;
      }
    }
  }
}