.spinning-logo-container{
    width: 50px;
    height: 50px;
}

.spinning-logo {
    transform-style: preserve-3d;
    animation: 10s spinning linear infinite;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.1s;

    > .face {
        position: absolute;
        top: 0;
        left: 0;

        &.front {
            width: 50px;
            height: 50px;
            transform: translateZ(2px);
        }

        &.back {
            width: 50px;
            height: 50px;
            transform: translateZ(-1.75px) translateX(3px);
            background-color: white;
            mask: url(../../../public/assets/logo2.svg);
            mask-repeat: no-repeat;

            > img {
                width: 100%;
                height: auto;
                transform: translate(0, 10px);
            }
        }

        &.side {
            width: 4px;
            height: 25px;

            &.e {
                background-color: white;
                transform: rotate3d(0, 1, 0, 90deg) translate3d(0, 12.5px, 44.5px);
            }

            &.w {
                background-color: white;
                transform: rotate3d(0, 1, 0, 90deg) translate3d(0, 12.5px, 1.5px);
            }

            &.nw {
                background-color: white;
                transform: rotateZ(120deg) rotateY(90deg) translate3d(0, -26px, -22.25px);
            }

            &.ne {
                background-color: white;
                transform: rotateZ(240deg) rotateY(90deg) translate3d(0px, 13.75px, -0.75px);
            }

            &.se {
                background-color: white;
                transform: rotateZ(120deg) rotateY(90deg) translate3d(0px, -26px, 20.75px);
            }

            &.sw {
                background-color: white;
                transform: rotateZ(240deg) rotateY(90deg) translate3d(0px, 13.75px, -43.75px);
            }
        }
    }
}

@keyframes spinning {
    0% {
        transform: rotate3d(0, 1, 0, 0deg)
    }
    100% {
        transform: rotate3d(0, 1, 0, 360deg)
    }
}
