
.section-introduction {
  width: 100%;
  padding: 1rem 0.5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  grid-gap: 0.5rem;
  min-height: calc(100vh - 70px - 1rem);

  .greeting {
    max-width: 450px;
    width: 100%;
    margin: 0 auto;
  }

  .picture {
    max-width: 450px;
    margin: 0 auto;
    mask-image: url(../../../public/assets/blob3.svg);
    mask-repeat: no-repeat;
    mask-position: center;
    mask-size: 65%;
    background-color: rgb(var(--color-foreground));
    transition: mask-size 0.2s ease-in-out;

    &:hover {
      mask-size: 60%;
    }

    img {
      width: 100%;
    }
  }

  .description {
    text-align: center;
    width: 100%;
    max-width: 300px;
    margin: 0.5rem auto;

    .contact-elements {
      margin: 1rem auto 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      grid-gap: 0.5rem;

      .redirect-btn {
        cursor: pointer;
        position: relative;

        &:hover {
          .logo-div {
            transform: scale(1.1);
          }
          .contact-tooltip {
            opacity: 1;
          }
        }
      }

      .logo-div {
        --logo-height: 30px;
        height: var(--logo-height);
        aspect-ratio: 1;
        transition: 0.2s;
        background: rgb(var(--color-foreground));

        &.github-logo {
          mask-image: url(../../../public/assets/github.svg);
          mask-repeat: no-repeat;
          mask-position: center;
        }

        &.linkedin-logo {
          mask-image: url(../../../public/assets/linkedin.svg);
          mask-repeat: no-repeat;
          mask-position: center;
        }

        &.mail-logo {
          mask-image: url(../../../public/assets/envelope.svg);
          mask-repeat: no-repeat;
          mask-position: center;
        }

        &.download-logo {
          height: calc(var(--logo-height) * 0.9);
          mask-image: url(../../../public/assets/download.svg);
          mask-repeat: no-repeat;
          mask-position: center;
        }
      }

      .contact-tooltip {
        pointer-events: none;
        transition: 0.2s;
        opacity: 0;
        position: absolute;
        padding: 0.5rem 1.5rem;
        border-radius: 5px;
        top: 150%;
        left: 50%;
        transform: translateX(-50%);
        background-color: rgb(var(--color-foreground));
        color: rgb(var(--color-background))
      }
    }
  }
}

@media all and (min-width: 700px) {
  .section-introduction {
    flex-direction: row;
    align-items: center;
    max-width: calc(700px + 1rem);
    margin: 0 auto;
    flex-wrap: wrap;

    .greeting, .picture {
      max-width: 340px;
    }
  }
}

@media all and (min-width: 1000px) {
  .section-introduction {
    flex-direction: row;
    align-items: center;
    max-width: 1000px;
    margin: 0 auto;

    .picture {
      max-width: 450px;
    }
  }
}